* {
  box-sizing: border-box;
}

body {
  background-color: #fff;
  margin: 0;
}

.spacer {
  flex-grow: 1;
}

.header {
  max-width: 70em;
  margin: 0 auto;
  padding: 1em;
  display: flex;
}

.header-item {
  margin: 0 1em;
  display: flex;
  align-items: center;
}

@media (max-width: 50em) {
  .header-item {
    margin: 0 0.5em;
  }
  .header-item img {
    width: 75%;
  }
}

.header-item a, .header-item button {
  background: none;
  border: none;
  color: #c5cbd7;
  cursor: pointer;
  font: 300 1.7em/1 "Rubik", sans-serif;
  outline: none;
  text-decoration: none;
  text-transform: lowercase;
  transition: color 0.1s;
}

@media (max-width: 50em) {
  .header-item a, .header-item button {
    font-size: 1.25em;
  }
}

.header-item a.active, .header-item button.active {
  color: #22c0e7;
}

.header-item a:hover, .header-item button:hover {
  color: #22c0e7;
}

@media not all and (max-width: 50em) {
  .header-item.rotatable {
    transition: transform 0.2s;
  }
  .header-item.rotatable:hover {
    transform: rotate(-7deg);
  }
}

.header-img {
  display: none;
}

@media not all and (max-width: 450px) {
  .header-img.header-img {
    display: block;
  }
}

.cart-txt {
  display: none;
  margin-right: 0.5em;
}

@media not all and (max-width: 450px) {
  .cart-txt.cart-txt {
    display: inline;
  }
}

.cart-size {
  background-color: #22c0e7;
  border-radius: 50px;
  color: #fff;
  padding: 0 0.3em;
}

.footer {
  max-width: 70em;
  margin: 2em auto 1em auto;
  display: flex;
}

@media (max-width: 71em) {
  .footer {
    margin: 2em 1em 1em 1em;
  }
}

.footer-item {
  color: #98a2b8;
  font: 300 1.7em/1 "Rubik", sans-serif;
  font-size: 1em;
}

@media (max-width: 50em) {
  .footer-item {
    font-size: 0.75em;
  }
}

.footer-item a {
  color: #22c0e7;
  text-decoration: none;
  transition: color 0.1s;
}

.footer-item a:hover {
  color: #67d4ee;
}

.shop-product-container {
  max-width: 70em;
  margin: 0 1em 1em 1em;
  position: relative;
}

@media not all and (max-width: 71em), (max-width: 50em) {
  .shop-product-container {
    margin: 0 auto 1em auto;
  }
}

.shop-product-cart {
  background-color: #ebd939;
  border: 1px solid #c4b214;
  border-radius: 50px;
  box-shadow: 0 0 7px #ebd939;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  transition: background-color 0.2s, border-color 0.2s, box-shadow 0.2s;
  width: 50px;
  height: 50px;
  padding: 0;
  position: absolute;
  top: -0.5em;
  left: 4em;
  z-index: 1;
}

.shop-product-cart img {
  width: 32px;
  margin: 8px;
}

@media (max-width: 50em) {
  .shop-product-cart {
    height: 30px;
    top: 0.25em;
    left: 0.25em;
  }
  .shop-product-cart img {
    width: 20px;
    margin: 4px;
  }
}

.shop-product-cart:hover {
  background-color: #fff;
  border-color: #eee;
  box-shadow: 0 0 7px #fff;
}

.shop-product-cart-txt {
  color: #334;
  font: 300 1.7em/1 "Rubik", sans-serif;
  width: 50px;
  display: block;
  line-height: 50px;
  text-align: center;
}

.shop-product-desc {
  color: #fff;
  font: 300 1.7em/1 "Rubik", sans-serif;
  font-style: italic;
  text-transform: lowercase;
  margin: 0 0 0.5em 0;
}

@media (max-width: 50em) {
  .shop-product-desc {
    font-size: 1.25em;
  }
}

.shop-product-title {
  color: #fff;
  font: 300 1.7em/1 "Rubik", sans-serif;
  font-size: 1em;
}

@media (max-width: 50em) {
  .shop-product-title {
    font-size: 0.75em;
  }
}

.shop-product {
  padding: 1em;
  display: flex;
  align-items: center;
}

@media not all and (max-width: 50em) {
  .shop-product {
    padding-left: 6em;
    padding-right: 6em;
  }
}

@media (max-width: 50em) {
  .shop-product {
    padding: 0.75em;
  }
}

.shop-product > *:not(:last-child) {
  margin-right: 2em;
}

@media (max-width: 50em) {
  .shop-product > *:not(:last-child) {
    margin-right: 0.75em;
  }
}

.shop-product[data-product-id="0"] {
  background-color: #334;
}

@media not all and (max-width: 50em) {
  .shop-product[data-product-id="0"] {
    clip-path: polygon(0 0, 95% 0, 100% 100%, 5% 100%);
  }
}

.shop-product[data-product-id="0"] .shop-product-desc::before {
  color: #22c0e7;
  content: '> ';
}

.shop-product[data-product-id="1"] {
  background-color: #22c0e7;
}

@media not all and (max-width: 50em) {
  .shop-product[data-product-id="1"] {
    clip-path: polygon(5% 0, 100% 0, 95% 100%, 0 100%);
  }
}

.shop-product[data-product-id="1"] .shop-product-desc::before {
  color: #334;
  content: '> ';
}

.shop-product-img {
  width: 15em;
}

@media (max-width: 71em) {
  .shop-product-img {
    width: 10em;
  }
}

@media (max-width: 50em) {
  .shop-product-img {
    width: 5em;
  }
}

.shop-product-action {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.shop-product-action-item {
  color: #fff;
  font: 300 1.7em/1 "Rubik", sans-serif;
  line-height: 2;
}

@media (max-width: 50em) {
  .shop-product-action-item {
    font-size: 1.25em;
  }
}

.shop-product-action-item:not(.sub) {
  border-bottom: 1px solid #fff;
}

.shop-product-action-item.sub {
  font-size: 1em;
}

@media (max-width: 50em) {
  .shop-product-action-item.sub {
    font-size: 0.75em;
  }
}

@media (max-width: 71em) {
  .shop-product-action-item.sub.short {
    display: block;
  }
}

@media not all and (max-width: 71em) {
  .shop-product-action-item.sub.long {
    display: block;
  }
}
